export const str = (name) => {
    const defaultLanguage  = 'zh-CN',
        userLang = navigator.language;
    if (strings[userLang] && strings[userLang][name])
        return strings[userLang][name];
    else
        return strings[defaultLanguage][name];
};

export default str;
